.wrapper {
    display: flex;
    flex-flow: column nowrap;
    align-content: center;
    gap: 2.5rem;
    width: auto;
}
.container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-items: center;
    align-items: center;
    text-align: center;
    gap: 2rem 1.5rem;
    margin-top: 1.5rem;
}
.containerTabs {
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-items: center;
}
.containerTabs > * {
    width: 100%;
}

.ingredientsContainer {
    display: flex;
    flex-flow: column nowrap;
    gap: 2.5rem;
    overflow-x: hidden;
    overflow-y: scroll;
    height: 600px;
    padding-right: 0.5rem;
}

.containerElement {
    width: auto;
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-content: center;
    gap: 4px;
    position: relative;
    cursor: grab;
    z-index: 5;
}
.containerPrice {
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-items: center;
    gap: 5px;
}

.containerImage {
    padding: 0em 1em;
    overflow: hidden;
    position: relative;
}

@media screen and (max-width: 600px) {
    .container {
        display: grid;
        grid-template-columns: 1fr;
    }
    .sectionsContainer {
        height: auto;
        overflow-y: auto;
        overflow-x: auto;
    }
}

.ingredientsContainer::-webkit-scrollbar {
    width: 0.4em;
}
.ingredientsContainer::-webkit-scrollbar-track {
    background: var(--background);
}

.ingredientsContainer::-webkit-scrollbar-thumb {
    background: var(--text-inactive-color);
}
.ingredientsContainer::-webkit-scrollbar-thumb:hover {
    background: var(--colors-interface-accent);
}
